import React, { Component, Fragment } from 'react';
import { Row, Col, Form, Select, Button, DatePicker, Input, Checkbox, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;

@Form.create({ name: 'formSerchHomeroomTeacher' })
class FormSerchHomeroomTeacher extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">
              Liệt kê danh sách
            </Button>
            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
              Xóa
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSerchHomeroomTeacher;



