import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Card, Row, Table, Popconfirm, Col, Select } from 'antd';
import { homeroomTeachers } from 'dump/data';
import FormSerchHomeroomTeacher from './FormSerchHomeroomTeacher';

const Option = Select.Option;
const columns = [
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'name',
  },
  {
    title: 'Số SV',
    dataIndex: 'numberStudent',
    key: 'nameEn',
  },
  {
    title: 'Niên khóa',
    dataIndex: 'year',
    key: 'fromScores',
  },
  {
    title: 'Khóa học',
    dataIndex: 'khoahoc',
    key: 'toScores',
  },
  {
    title: 'Tên khoa',
    dataIndex: 'khoa',
    key: 'toScores',
  },
  {
    title: 'Chuyên ngành',
    dataIndex: 'cn',
    key: 'toScores',
  },
  {
    title: 'Chủ nhiệm lớp',
    dataIndex: 'gvcn',
    render: (text, record) =>
      homeroomTeachers.length >= 1 ? (
        <Fragment>
          <Select defaultValue="lucy" style={{ width: '100%' }}>
            <Option value="jack">Trần Văn A</Option>
            <Option value="lucy">Nguyễn Văn B</Option>
            <Option value="Yiminghe">Trần Văn C</Option>
          </Select>
        </Fragment>
      ) : null,
  },
  {
    title: 'SĐT',
    dataIndex: 'phone',
  },
];

class ListHomeroomTeacherPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Giáo viên chủ nhiệm">
        <Row>
          <Col span={24}>
            <FormSerchHomeroomTeacher />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={homeroomTeachers} columns={columns} />
          </Col>
          <Col offset={8} span={12}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={this.cancelUpload} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListHomeroomTeacherPage />
  </Page>
);



